.tech {
  text-align: center;
}
.tech h3 {
  padding-top: 15px;
}

.tech-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: center;
  width: 100%;
}

.tech-item {
  box-sizing: border-box;
  padding: 5px;
  margin: 20px 30px;
  font-weight: bold;
}
.tech-item .skills-container {
  position: relative;
  display: block;
  width: 86.5px;
  background-color: #ddd;
  border-radius: 20px;
  visibility: hidden;
  opacity: 0;
}
.tech-item .skills-container .tooltiptext {
  width: 40%;
  background-color: #555;
  color: #fff;
  font-size: 12px;
  text-align: center;
  border-radius: 6px;
  padding: 2px 0;
  position: absolute;
  z-index: 1;
  top: 175%;
  transition: 0.3s;
}
.tech-item .skills-container .tooltiptext::before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #555 transparent;
}
.tech-item .skills-container .skills {
  border-radius: 20px;
  height: 9px;
  background-color: #4caf50;
}
.tech-item:hover, .tech-item:hover .skills-container {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
}

