@media screen and (max-width: 650px) {
  .tech-container {
    grid-template-columns: auto auto auto;
  }

  .tech-item {
    padding: 5px;
    margin: 20px 20px;
  }
}
@media screen and (max-width: 420px) {
  .tech h3 {
    font-size: 18px;
  }

  .tech-container {
    grid-template-columns: auto auto;
  }

  .tech-item {
    padding: 5px;
    margin: 10px 15px;
  }
}
