.projects {
  text-align: center;
}
.projects h3 {
  padding: 20px 5px;
}

.project-img {
  box-sizing: border-box;
  padding-top: 40px;
}
.project-img img {
  width: 450px;
  height: 300px;
  border-radius: 20px;
  border: 1px solid #eaecef;
}

.project-info {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: center;
  margin-bottom: 30px;
  width: 100%;
}
.project-info .project-container {
  text-align: center;
}
.project-info .project-container h4 {
  margin-bottom: 15px;
}
.project-info .project-disc,
.project-info .project-disc + p,
.project-info .project-stack,
.project-info .project-links {
  text-align: left;
}
.project-info .project-stack .stack-item {
  display: inline-block;
  padding: 0 5px;
  margin: 0 10px;
}
.project-info .project-stack .stack-item p {
  text-align: center;
}
.project-info .project-stack img {
  display: block;
  margin: 0 auto;
}
.project-info .project-links {
  margin-top: 20px;
}
.project-info .project-links a {
  display: inline-block;
  text-decoration: none;
  border-radius: 10px;
  text-decoration: none;
  background-color: #f6f8fa;
  border: 1px solid #21262d;
  color: #ff6984;
}
.project-info .project-links a:hover {
  background-color: #354757;
}
.project-info .project-links .project-github {
  margin-right: 15px;
}

@media screen and (min-width: 1024px) {
  .project-container {
    width: 460px;
    margin: 20px 40px;
  }

  .project-links a {
    padding: 8px 15px;
  }
}
@media screen and (max-width: 1024px) {
  .project-info {
    grid-template-columns: auto;
  }

  .project-container {
    width: 460px;
    margin: 20px 40px;
  }

  .project-links a {
    padding: 8px 15px;
  }
}
@media screen and (max-width: 580px) {
  .project-img {
    margin: 20px auto;
    width: 235px;
  }

  .project-img > img {
    width: 225px;
    height: 150px;
  }

  .project-container {
    width: 250px;
    margin: 10px 15px;
    padding: 7px;
  }

  .project-stack {
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: center;
  }

  .project-links {
    margin-top: 20px;
  }
  .project-links a {
    padding: 5px 10px;
  }
  .project-links .project-github {
    margin-bottom: 15px;
  }
}
.dark .projects {
  color: #c9d1d9;
  background-color: #121e2a;
  border-bottom: 1px solid #21262d;
}
.dark .projects .project-github,
.dark .projects .project-deploy {
  color: #ff6984;
  background-color: #222d37;
}
.dark .projects .project-github:hover,
.dark .projects .project-deploy:hover {
  background-color: #354757;
}